


import { DriveFolderUploadOutlined } from "@mui/icons-material";
import React, { useState } from "react";
import Navbar from "../../components/navbar/Navbar";
import "./editProfile.css";

const EditProfile = () => {
    // Initialize state to store user profile data
    const [profileData, setProfileData] = useState({
        name: "Amber Logan",
        email: "amber_logan@gmail.com",
        zipcode: "77005",
        phone: "+4 123 456 789",
        password: "********", // Display password as asterisks
        avatar: "/assets/person/user.jpg",
    });

    // State to track if the user is currently editing the profile
    const [isEditing, setIsEditing] = useState(false);

    // Handler for updating the user profile data
    const updateProfile = () => {
        // Implement logic to update the user's profile using the form data
        // You can make an API call or update the state accordingly
        // For now, this example just logs the updated data
        console.log("Updated Profile Data:", profileData);

        // Set isEditing to false after successful update
        setIsEditing(false);
    };

    // Handler for handling file input change (for avatar)
    const handleFileChange = (event) => {
        const file = event.target.files[0];
        // Implement logic to upload the selected file (image) here
        // For now, this example just logs the selected file
        console.log("Selected File:", file);
    };

    // Handler for input changes (except avatar)
    const handleInputChange = (event, fieldName) => {
        const value = event.target.value;
        setProfileData({
            ...profileData,
            [fieldName]: value,
        });
    };

    return (
        <div className="editProfile">
            <Navbar />
            <div className="editProfileWrapper">
                <div className="profileRight">
                    <div className="profileRightTop">
                        {/* Profile Cover and User Avatar */}
                        <div className="profileCover">
                            <img src="/assets/profileCover/profilecover.jpg" alt="" className="profileCoverImg" />
                            <img src={profileData.avatar} alt="" className="profileUserImg" />
                            {isEditing ? (
                                <input type="file" id="file" style={{ display: "none" }} onChange={handleFileChange} />
                            ) : null}

                        </div>
                        {/* Profile Information */}
                        <div className="profileInfo">
                            <h4 className="profileInfoName">{profileData.name}</h4>
                            <span className="profileInfoDesc">Hi Friends!</span>
                        </div>
                    </div>
                    {/* Edit Profile Form */}
                    <div className="editprofileRightBottom">
                        <div className="top">
                            <h1>Edit User Profile</h1>
                        </div>
                        <div className="bottom">
                            {/* Left Side: Display current user info */}
                            <div className="left">
                                <div className="nowUserInfo">
                                    <h1>Current User Info</h1>
                                </div>
                                <img src={profileData.avatar} alt="" />
                                {isEditing ? (
                                    <form>
                                        {/* Display existing profile data */}
                                        <div className="formInput">
                                            <label>Name</label>
                                            <input type="text" value={profileData.name} readOnly />
                                        </div>
                                        <div className="formInput">
                                            <label>Password</label>
                                            <input type="password" value={profileData.password} readOnly />
                                        </div>
                                        <div className="formInput">
                                            <label>Email</label>
                                            <input type="email" value={profileData.email} readOnly />
                                        </div>
                                        <div className="formInput">
                                            <label>Phone</label>
                                            <input type="text" value={profileData.phone} readOnly />
                                        </div>
                                        <div className="formInput">
                                            <label>Zipcode</label>
                                            <input type="text" value={profileData.zipcode} readOnly />
                                        </div>
                                    </form>
                                ) : null}
                            </div>
                            {/* Right Side: Edit user info */}
                            <div className="right">
                                <img src={"./assets/profileCover/DefaultProfile.jpg"} alt="" />
                                <form>
                                    <div className="formInput">
                                        <label htmlFor="file">
                                            Change Avatar <DriveFolderUploadOutlined className="icon" />
                                        </label>
                                        {isEditing ? (
                                            <input type="file" id="file" style={{ display: "none" }} onChange={handleFileChange} />
                                        ) : null}
                                    </div>
                                    <div className="formInput">
                                        <label>Name</label>
                                        <input
                                            type="text"
                                            value={profileData.name}
                                            readOnly={!isEditing}
                                            placeholder="Jane Doe"
                                            onChange={(e) => handleInputChange(e, "name")}
                                        />
                                    </div>
                                    <div className="formInput">
                                        <label>Password</label>
                                        <input
                                            type="password"
                                            value={profileData.password}
                                            readOnly={!isEditing}
                                            placeholder="********"
                                            onChange={(e) => handleInputChange(e, "password")}
                                        />
                                    </div>
                                    <div className="formInput">
                                        <label>Email</label>
                                        <input
                                            type="email"
                                            value={profileData.email}
                                            readOnly={!isEditing}
                                            placeholder="jane_doe@gmail.com"
                                            onChange={(e) => handleInputChange(e, "email")}
                                        />
                                    </div>
                                    <div className="formInput">
                                        <label>Phone</label>
                                        <input
                                            type="text"
                                            value={profileData.phone}
                                            readOnly={!isEditing}
                                            placeholder="+4 123 456 789"
                                            onChange={(e) => handleInputChange(e, "phone")}
                                        />
                                    </div>
                                    <div className="formInput">
                                        <label>Zipcode</label>
                                        <input
                                            type="text"
                                            value={profileData.zipcode}
                                            readOnly={!isEditing}
                                            placeholder="77005"
                                            onChange={(e) => handleInputChange(e, "zipcode")}
                                        />
                                    </div>
                                    {isEditing ? (
                                        <button type="button" className="updateButton" onClick={updateProfile}>
                                            Update Profile
                                        </button>
                                    ) : (
                                        <button type="button" onClick={() => setIsEditing(true)}>
                                            Edit Profile
                                        </button>
                                    )}
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EditProfile;
